@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700;800&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

body {
  line-height: 1.7;
}
.heading {
  width: 100%;
  height: 100vh;
  background-image: linear-gradient(rgba(12, 3, 51, 0.3), rgba(12, 3, 51, 0.3));
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.content {
  text-align: center;
}
.content h1 {
  font-size: 4rem;
  color: #fff;
  font-weight: 700;
}
.content h5 {
  color: #fff;
  font-size: 1.7rem;
  padding: 25px;
}
.content p {
  color: #fff;
  font-size: 20px;
}

video {
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
h2 {
  font-weight: 700;
}
.work-with-us {
  text-align: center;
  padding: 80px 8%;
}

.work-with-us h2,
.our-story h2 {
  font-size: 1.8rem;
}
.work-with-us p,
.our-story p,
.work-with-us__icon {
  font-size: 1.2rem;
}
.work-with-us__cards {
  display: flex;
  justify-content: space-between;
}
.work-with-us__card i {
  font-size: 1.6rem;
  padding: 40px 0;
}

.our-story {
  background-color: rgb(19, 19, 19);
  color: rgb(248, 249, 250);
  padding: 20px 8%;
  font-size: 1.2rem;
  padding: 80px 0;
  width: 100%;
}
.our-story h2,
.our-story h3 {
  text-align: center;
  margin: 25px 0;
}
.our-story h3 {
  word-spacing: 2px;
}
.our-story p {
  line-height: 2;
  color: #f7f9f7;
  font-size: 1.1rem;
}

.our-story__team {
  display: flex;
  padding: 20px 8%;
  justify-content: space-between;
  grid-column-gap: 50px;
  -webkit-column-gap: 50px;
          column-gap: 50px;
}
.our-story__team img {
  width: 100%;
  object-fit: contain;
}
.our-story__team p {
  text-align: justify;
}

@media screen and (max-width: 940px) {
  .content h1 {
    font-size: 2.8rem;
  }
  .content a {
    font-size: 20px;

    padding: 12px 40px;
  }
  .work-with-us__cards {
    flex-direction: column;
  }
  .our-story {
    padding: 40px 0;
  }
  .work-with-us h2,
  .our-story h2 {
    font-size: 1.6rem;
  }
  .our-story h3 {
    font-size: 1.2rem;
    padding: 0 20px;
  }
  .our-story__team {
    flex-direction: column;
    font-size: 14px;
    line-height: 1.5;
    grid-gap: 40px;
    gap: 40px;
  }
  .our-story__team p {
    font-size: 0.88rem;
    margin-bottom: 5px;
  }
}

.who-we-are {
  background-color: rgb(19, 19, 19);
  /* font-size: 1.2rem; */
  padding: 150px 8%;
  width: 100%;
}
.who-we-are h1 {
  text-align: center;
  color: #f1aa30;
  margin: 40px 0 50px 0;
  font-size: 2.2rem;
}

.who-we-are p {
  color: #f7f9f7;
  text-align: justify;
  font-size: 1.1rem;
  line-height: 2;
}

.who-we-are__team {
  display: flex;
  justify-content: space-between;
  grid-column-gap: 60px;
  -webkit-column-gap: 60px;
          column-gap: 60px;
}
.image {
  height: 500px;
  object-fit: contain;
}

.our-mission {
  background-image: linear-gradient(
      to bottom right,
      rgba(143, 142, 142, 0.3),
      rgb(15, 14, 14)
    ),
    url(/react-project-website/static/media/our-mission.8ebe3265.png);
  background-repeat: no-repeat;
  background-size: cover;
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: 100%;
  padding: 130px 8%;
  line-height: 1.6;
  grid-column-gap: 10px;
  -webkit-column-gap: 10px;
          column-gap: 10px;
  color: #fff;
}

.collections h2 {
  line-height: 1.5;
  font-size: 2.3rem;
  width: 70%;
}
.collection h1 {
  font-size: 2.3rem;
  color: #f1aa30;
}
.collection p {
  width: 70%;
  font-size: 1.22rem;
}

.collections-card {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-row-gap: 40px;
  row-gap: 40px;
  padding: 40px 0;
}
.our-mission__paragraph {
  margin-top: 30px;
}
.our-mission__paragraph p {
  line-height: 2;
  font-size: 1.1rem;
  text-align: justify;
  margin-bottom: 12px;
}

/* Work with the best */

.work-with-best {
  color: #fff;
  text-align: center;
  background-color: #111;
  line-height: 2;
  padding: 80px 0;
}

.work-with-best h1 {
  font-size: 2rem;
}
.work-with__body {
  display: flex;
  justify-content: center;
}
.work-with-item {
  margin: 60px;
}
.work-with__image img {
  height: 75px;
  width: 75px;
  border-radius: 50%;
}
.work-with-item p {
  font-size: 1.2rem;
  padding-top: 30px;
  color: #f7f9f7;
}
.work-with__cards {
  display: flex;
  justify-content: center;
}
.work-with__cards .card {
  padding: 40px;
}

@media (max-width: 968px) {
  .who-we-are__team {
    flex-direction: column;
    grid-row-gap: 20px;
    row-gap: 20px;
  }
  .who-we-are h1 {
    text-align: center;
    margin-bottom: 30px;
  }
  .who-we-are p {
    font-size: 0.9rem;
    margin-bottom: 5px;
  }
  .image {
    order: 1;
    width: 100%;
    height: 350px;
  }
  .who-we-are__card {
    order: 2;
  }

  .our-mission {
    grid-template-columns: 1fr;
    padding: 70px 8%;
  }
  .collections h2 {
    font-size: 1.8rem;
    width: 100%;
    text-align: center;
    line-height: 1.6;
  }

  .collections-card {
    grid-template-columns: 1fr;
    text-align: center;
  }
  .collection p {
    width: 100%;
    font-size: 1.22rem;
  }

  .our-mission__paragraph {
    margin-top: 0;
  }
  .our-mission__paragraph p {
    font-size: 0.9rem;
    margin-bottom: 5px;
  }

  .work-with-best h1 {
    font-size: 1.6rem;
    padding: 0 20px;
  }
  .work-with__body {
    flex-direction: column;
  }
  .work-with-item {
    margin: 20px;
  }
}

.our-client {
  background-color: rgb(19, 19, 19);
  padding: 150px 8%;
}

.our-client h1 {
  text-align: center;
  color: #f1aa30;
  margin: 40px 0 30px 0;
  font-size: 2.2rem;
}

.our-client .paragraph {
  text-align: center;
  font-size: 1.4rem;
  margin-bottom: 20px;
  color: #fff;
}

.column {
  margin: 70px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  grid-gap: 3rem 1.8rem;
  gap: 3rem 1.8rem;
}

.card {
  width: 100%;
  display: flex;
  flex-direction: column;
  box-shadow: 0 2px 5px rgb(59, 58, 58) !important;
}
.button {
  background-color: #f6a210;
  border: none;
  align-self: flex-start;
  margin-top: auto;
}
.button:hover {
  background-color: #efb042;
}
.card-body {
  display: flex;
  flex-direction: column;
  padding: 1.7rem !important;
}

.overflow {
  overflow: hidden;
}
.card-img-top {
  border-radius: 50%;
  padding: 15px 15px 0 15px;
  -webkit-transform: scale(1);
          transform: scale(1);
  transition: -webkit-transform 0.5s ease;
  transition: transform 0.5s ease;
  transition: transform 0.5s ease, -webkit-transform 0.5s ease;
}

.card-img-top:hover {
  -webkit-transform: scale(1.04);
          transform: scale(1.04);
}
@media screen and (max-width: 940px) {
  .our-client__heading {
    font-size: 2rem;
    margin-bottom: 15px;
  }

  .our-client .paragraph {
    font-size: 1rem;
    margin-bottom: 30px;
  }
  .column {
    grid-gap: 1.5rem;
    gap: 1.5rem;
    margin: 20px;
  }
  .card-body {
    font-size: 0.9rem;
    padding: 1.1rem !important;
  }
  .our-client {
    padding: 60px 8%;
  }
  .card-img-top {
    display: none;
  }
}

.contact-container {
  background-color: rgb(0, 0, 0);
  padding: 150px 8% 100px;
  color: #f7f9f7;
}

.contact-heading {
  text-align: center;
  margin-bottom: 60px;
}

.contact-heading h1 {
  color: #f1aa30;
  margin-bottom: 20px;
  font-size: 2.2rem;
}
.contact-heading p {
  font-size: 1.4rem;
  color: #fff;
}

.contact-section {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 60px;
  gap: 60px;
}
.contact-col1 {
  text-align: center;
}
.contact-col1 p,
.contact-col2 p {
  font-size: 1.1rem;
  margin: 20px 0 45px;
  text-align: center;
}
.contact-image img {
  margin-bottom: 25px;
}

.contact-form {
  font-family: inherit;
  color: #fff;
}

.form-group {
  margin-bottom: 1.25rem;
  padding: 0.2rem;
  display: grid;
  grid-template-columns: 0.17fr 1fr;
  font-family: inherit;
}

.form-control {
  padding: 0.4rem 0.75rem;
  font-size: 0.9rem;
  background: transparent;
  outline: none;
  border: 1px solid #f7f9f7;
  border-radius: 8px;
  color: #f7f9f7 !important;
}

.input-textarea {
  min-height: 140px;
  background-color: transparent;
  border: 1px solid #f7f7f7;
  border-radius: 8px;
  padding: 0.625rem;
  resize: vertical;
  color: #f7f7f7;
}

.cta__book,
.send-button {
  background-color: #f5b13d;
  border: none;
  font-size: 1rem;
  letter-spacing: 2px;
  text-decoration: none;
  padding: 6px 12px;
  border-radius: 5px;
  margin: auto;
  color: #000;
}
.cta__book:hover,
.send-button:hover {
  background-color: #f2a014;
  color: #000;
}

@media (max-width: 968px) {
  .contact-section {
    grid-template-columns: 1fr;
    grid-gap: 20px;
    gap: 20px;
  }
  .contact-heading {
    font-size: 0.95rem;
    margin-bottom: 35px;
  }
  .contact-container h1 {
    font-size: 2rem;
    margin-bottom: 14px;
  }
  .contact-container p {
    font-size: 1.2rem;
    padding: 10px;
  }
  .contact-col1 {
    font-size: 0.9rem;
  }
  .contact-col1 p {
    margin: 0 0 30px;
  }
  .contact-col2 p {
    margin-bottom: 30px;
  }
  .contact-image img {
    width: 80%;
  }

  .form-group {
    grid-template-columns: 0.3fr 1fr;
    font-size: 0.9rem;
  }
}

.carrers {
  background-color: rgb(19, 19, 19);
  padding: 150px 8%;
  line-height: 1.5;
}

.carrer-heading {
  text-align: center;
  margin-bottom: 50px;
}
.carrers h1 {
  color: #f1aa30;
  margin-bottom: 20px;
  font-size: 2.2rem;
}
.carrer-heading p {
  font-size: 1.4rem;
  color: #fff;
}
.carrer-heading a {
  color: #f1aa30;
}
.carrer-heading a:hover {
  color: #f6a315;
}
.carrer-body {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 10px;
  -webkit-column-gap: 10px;
          column-gap: 10px;
  align-items: center;
}
.carrer-image img {
  width: 80%;
}
.carrer-body p {
  line-height: 2;
  font-size: 1.1rem;
  text-align: justify;
  color: #f7f9f7;
}
@media screen and (max-width: 940px) {
  .carrer-image img {
    display: none;
  }
  .carrer-heading p {
    font-size: 1.25rem;
  }
  .carrer-body p {
    font-size: 0.85rem;
  }
  .carrer-body {
    grid-template-columns: 1fr;
  }
}

.nav-header {
  position: fixed;
  /* height: 80px; */
  width: 100%;
  top: 0;
  left: 0;
  z-index: 10;
  transition: 0.3s ease-in;
  overflow: hidden;
}
.nav-header-bg {
  background-color: rgba(0, 0, 0, 0.9);
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 6%;
}
.navbar ul li {
  list-style: none;
  display: inline-block;
  margin-left: 40px;
}
.navbar ul li a {
  text-decoration: none;
  color: #fff;
  font-size: 15px;
  font-weight: 500;
}
.navbar ul li a:hover {
  color: #f5b13d;
}

.navbar .logo img {
  width: 65px;
  margin-bottom: 0;
}
.nav-menu {
  display: flex;
}
.hamburger {
  display: none;
}
.cta {
  background-color: #f5b13d;
  border: none;
  font-family: inherit;
  font-size: 14px !important;
  padding: 7px 10px;
  border-radius: 6px;
  color: #000 !important;
}
.cta:hover {
  background-color: #f19c07;
}

@media screen and (max-width: 940px) {
  .nav-menu {
    position: fixed;
    left: -100%;
    top: 80px;
    flex-direction: column;
    justify-content: center;
    background-color: rgb(19, 19, 19);
    width: 100%;
    height: 100%;
    text-align: center;
    transition: 0.4s;
    z-index: 2;
  }

  .nav-menu.active {
    left: 0;
  }

  .navbar ul li {
    padding: 1.3rem;

    justify-content: center;
  }
  .navbar ul li a {
    font-size: 1.2rem;
  }
  .hamburger {
    display: flex;
  }
}

.container {
  padding: 20px 8%;
  margin: 50px auto 0;
}
.row {
  display: grid;
  grid-template-columns: 1.5fr 1fr 1fr 2fr;
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}
.footer-col {
  padding: 10px;
}

.footer-col h4 {
  font-size: 1.125rem;
  color: #222;
  margin-bottom: 30px;
  font-weight: 600;
}

.logo img {
  width: 80px;
  margin-bottom: 10px;
}

.footer-col ul li:not(:last-child) {
  margin-bottom: 10px;
}
.footer-col ul li a {
  font-size: 1rem;
  font-weight: 400;
  text-decoration: none;
  color: #333;
  display: block;
  transition: all 0.3s ease;
}
.footer-col ul li a:hover {
  color: #000;
  padding-left: 4px;
}
.footer-col .social-links a {
  display: inline-block;
  font-size: 20px;
  height: 40px;
  width: 40px;
  margin: 20px 10px 10px 0;
  text-align: center;
  line-height: 40px;
  color: #333;
  transition: all 0.5s ease;
}
.footer-col .social-links a:hover {
  color: #222;
  padding-left: 5px;
}
.footer-form {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 10px;
  border-bottom: 1px solid #ccc;
}
.footer-form .far {
  font-size: 18px;
  margin-right: 10px;
  color: #333;
}
.footer-form input {
  width: 100%;
  background: transparent;
  outline: none;
  line-height: 2;
  font-size: 14px;
  border: 0;
}
::-webkit-input-placeholder {
  color: #ccc;
}
::placeholder {
  color: #ccc;
}
.footer-form button {
  background: transparent;
  border: 0;
  outline: none;
  cursor: pointer;
}
.footer-form button .fas {
  font-size: 16px;
  color: #333;
}
.footer-form button .fas:hover {
  padding-left: 5px;
  color: #000;
}

hr {
  border: 0;
  border-bottom: 1px solid #ccc;
  margin: 22px auto;
}
.copyright {
  text-align: center;
  font-size: 14px;
}

@media screen and (max-width: 940px) {
  .row {
    grid-template-columns: 0.8fr 1fr 1fr 1.8fr;
    grid-gap: 10px;
    gap: 10px;
    font-size: 0.8rem;
  }
  .col-newsletter {
    grid-row: 2/3;
    grid-column: 1/5;
  }
  .col-company {
    grid-column: 3/4;
  }
  .col-service {
    grid-column: 4/5;
  }
  .col-logo {
    grid-column: 1/3;
  }
  .col-logo p {
    font-size: 0.8rem;
  }

  .footer-col ul li a {
    font-size: 0.8rem;
  }
  .logo img {
    width: 68px;
  }

  .footer-col h4 {
    font-size: 1rem;
    margin-top: 10px;
    margin-bottom: 20px;
  }
  .footer-col .social-links {
    text-align: center;
  }
}

